.nav-icon-5{
    width: 35px;
    height: 30px;
    margin: 10px 10px;
    position: relative;
    cursor: pointer;
    display: inline-block;
  }
  .nav-icon-5 span{
    background-color:#fff;
    position: absolute;
    border-radius: 2px;
    transition: .3s cubic-bezier(.8, .5, .2, 1.4);
    width:100%;
    height: 4px;
    transition-duration: 500ms
  }
  .nav-icon-5 span:nth-child(1){
    top:0px;
    left: 0px;
  }
  .nav-icon-5 span:nth-child(2){
    top:13px;
    left: 0px;
    opacity:1;
  }
  .nav-icon-5 span:nth-child(3){
    bottom:0px;
    left: 0px;
  }
  .nav-icon-5:not(.open):hover span:nth-child(1){
    transform: rotate(-3deg) scaleY(1.1);
  }
  .nav-icon-5:not(.open):hover span:nth-child(2){
    transform: rotate(3deg) scaleY(1.1);
  }
  .nav-icon-5:not(.open):hover span:nth-child(3){
    transform: rotate(-4deg) scaleY(1.1);
  }
  .nav-icon-5.open span:nth-child(1){
    transform: rotate(45deg);
    top: 13px;
  }
  .nav-icon-5.open span:nth-child(2){
    opacity:0;
  }
  .nav-icon-5.open span:nth-child(3){
    transform: rotate(-45deg);
    top: 13px;
  }
  .navlink{
        position: absolute;
        top: -700px;
        left: -2000px;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        transition: all .6s ease;
}
.navlink.active{z-index: 4;
    width: 100%;
    display: block;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    top: 10%;
    left: 0;
    right: 0;
    text-align: center;
   
}
a{
    margin-top: 1rem;
    font-size: 1.3rem;
    display: block;
}
    @media(min-width: 768px){
        .burger{display:none;
    }
    .navlink{z-index: 4;
        position: initial;
        margin: 0;
    }
    a{
        
        font-size: 1rem;
        display: inline;
    }
  }
.burger{
  z-index: 4;
}
  .initial{
    position: absolute;
    background-color: rgb(29, 23, 23);
    width: 100%;
    height: 35%;
    top: -700px;
    left: -1000px;
    z-index: 3;
    transition: all .6s ease;
  }
  .initial.active{
    border-radius: 0 0 80% 0;
    width: 100%;
    height: 35%;
    top: 0;
    left: 0;  
    
}
